/* 
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com 
Twitter: @rich_clark
*/

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-size: 100%;
	vertical-align: baseline;
	background: transparent;
}

body {
	line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

nav ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: "";
	content: none;
}

a {
	margin: 0;
	padding: 0;
	font-size: 100%;
	vertical-align: baseline;
	background: transparent;
}

/* change colours to suit your needs */
ins {
	background-color: #ff9;
	color: #000;
	text-decoration: none;
}

/* change colours to suit your needs */
mark {
	background-color: #ff9;
	color: #000;
	font-style: italic;
	font-weight: bold;
}

del {
	text-decoration: line-through;
}

abbr[title],
dfn[title] {
	border-bottom: 1px dotted;
	cursor: help;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
	display: block;
	height: 1px;
	border: 0;
	border-top: 1px solid #cccccc;
	margin: 1em 0;
	padding: 0;
}

input,
select {
	vertical-align: middle;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

a {
	text-decoration: none;
}
/* end of reset styling */
@font-face {
	font-family: "Roboto", sans-serif;
	src: url("./fake-receipt.otf");
}
body {
	user-select: none;
	overflow: hidden;
}
#content {
	overflow: hidden;
}
#main-hud {
	width: 500px;
	border: 2px solid transparent; /* Start with a transparent border */
	border-radius: 10px;
	position: absolute;
	bottom: 20px;
	left: 20px;
	padding: 20px;
	color: rgba(0, 152, 190, 1);
}

#name {
	font-size: 23px;
	font-family: "Roboto", sans-serif;
	color: white;
}
.arrow-btn {
	font-size: 30px;
	cursor: pointer;
	color: white;
	transition: 0.5s ease-out;
	font-family: "Roboto", sans-serif;
	font-weight: 400;
	border: 1px solid white;
	width: 50px;
	height: 40px;
	border-radius: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}
.arrow-btn img {
	height: 30%;
}

#hud-arrows {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
#data-area {
	font-size: 15px;
	line-height: 20px;
	font-family: "Roboto", sans-serif;
	color: rgb(0, 152, 190);
	height: 170px;
	margin-top: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: #0a0a0a;
	padding: 20px;
	border-radius: 10px;
	box-sizing: border-box;
}
#data-area p {
	width: 100%;
	display: flex;
	justify-content: space-between;
}
span {
	color: white;
}
#threat {
	color: rgb(143, 246, 126);
	justify-content: center;
}
#control-area {
	position: absolute;
	top: 20px;
	left: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.main-btn {
	border: 1px solid white;
	height: 40px;
	min-width: 40px;
	border-radius: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 10px;
	cursor: pointer;
	font-family: "Roboto", sans-serif;
}
.main-btn img {
	height: 50%;
}
.main-btn p {
	color: white;
	font-size: 14px;
}
.main-btn p span {
	font-size: 12px;
}
#highlight-color-value {
	background: rgb(255, 0, 0);
	height: 50%;
	width: 50%;
	border-radius: 50px;
}
#tl-btn {
	display: none;
}
#asteroid-list {
	width: 60px;
	height: 100%;
	border: 2px solid transparent; /* Start with a transparent border */
	position: absolute;
	top: 0px;
	right: 0px;
	color: rgba(0, 152, 190, 1);
	font-weight: 700;
	background-color: #0a0a0a;
	border-left: 2px solid rgba(0, 152, 190, 1);
	padding-left: 60px;
	padding-top: 30px;
	box-sizing: border-box;
	overflow: hidden;
	transition: width 0.3s;
}
#al-toggle {
	position: absolute;
	height: 100%;
	width: 60px;
	left: 0px;
	top: 0px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: "Roboto", sans-serif;
	cursor: pointer;
}
#al-toggle p {
	margin-bottom: 5px;
	color: white;
}
#al-toggle:hover {
	background-color: #1f1f1f;
}
#list-title {
	font-family: "Roboto", sans-serif;
	font-size: 23px;
	color: white;
}
#list {
	list-style: none;
	height: 95%;
	margin-top: 15px;
	-ms-overflow-style: none; /* for Internet Explorer, Edge */
	scrollbar-width: none; /* for Firefox */
	overflow-y: scroll;
	font-family: "Roboto", sans-serif;
}
#list li {
	cursor: pointer;
	margin: 10px 0px;
	font-size: 16px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.list-dot {
	height: 10px;
	width: 10px;
	border-radius: 10px;
	margin-right: 20px;
}
#al-close-btn {
	width: 30px;
	height: 30px;
	display: flex;
	justify-content: center;
	color: white;
	align-items: center;
	border: 1px solid white;
	border-radius: 50px;
	position: relative;
	position: absolute;
	top: 20px;
	right: 28px;
	cursor: pointer;
	display: none;
}
#al-close-btn img {
	width: 40%;
}

@media only screen and (max-width: 1000px) {
	#control-area {
		flex-direction: row;
	}
	.main-btn {
		margin-right: 10px;
		margin-left: 10px;
	}
	#al-toggle {
		display: none;
	}
	#asteroid-list {
		padding: 0px;
		width: 0px;
		border: none;
	}
	#tl-btn {
		display: flex;
	}
	#al-close-btn {
		display: flex;
	}
	#control-area {
		left: 50%;
		transform: translateX(-50%);
	}
	#main-hud {
		width: 100%;
		left: 0px;
		box-sizing: border-box;
		bottom: 0px;
	}
}
@media only screen and (max-width: 500px) {
	#name {
		font-size: 20px;
	}
	.arrow-btn {
		height: 30px;
		width: 40px;
	}
}
